:root {
    --bluegray-500: hsla(228, 20%, 50%, 1);
    --bluegray-700: hsla(228, 20%, 30%, 1);
    --various-blue: hsla(226, 50%, 39%, 1);
    --various-light-orange: hsla(39, 82%, 53%, 1);
    --various-orange: hsla(17, 73%, 58%, 1);
    --various-purple: hsla(274, 38%, 44%, 1);
    --various-rose: hsla(346, 64%, 57%, 1);
    --various-violet: hsla(319, 48%, 46%, 1);

    /* primary-contrast are not alias colors by design system! */
    --primary-contrast-25: var(--primary-850);
    --primary-contrast-50: var(--primary-850);
    --primary-contrast-100: var(--primary-850);
    --primary-contrast-200: var(--primary-850);
    --primary-contrast-300: var(--primary-850);
    --primary-contrast-400: var(--primary-850);
    --primary-contrast-500: var(--primary-850);
    --primary-contrast-600: var(--gray-0);
    --primary-contrast-700: var(--gray-0);
    --primary-contrast-800: var(--gray-0);
    --primary-contrast-850: var(--gray-0);
    --primary-contrast-900: var(--gray-0);

    --additional-500: var(--bluegray-500);
    --additional-700: var(--bluegray-700);

    --data-1: var(--various-blue);
    --data-2: var(--various-purple);
    --data-3: var(--various-violet);
    --data-4: var(--various-rose);
    --data-5: var(--various-orange);
    --data-6: var(--various-light-orange);

    /* -------------- STATE LAYER COLORS -------------------- */
    /* these variables expected to follow strict naming convention defined in ../utils/stateLayer.ts */

    --dark-on-white-color: var(--neutral-1000);
    --dark-on-white-opacity-1: 4%;
    --dark-on-white-opacity-2: 6%;
    --dark-on-white-opacity-3: 8%;

    --dark-on-teal-color: var(--neutral-1000);
    --dark-on-teal-opacity-1: 10%;
    --dark-on-teal-opacity-2: 18%;
    --dark-on-teal-opacity-3: 24%;

    --teal-on-white-color: var(--primary-500);
    --teal-on-white-opacity-1: 8%;
    --teal-on-white-opacity-2: 12%;
    --teal-on-white-opacity-3: 24%;

    --white-on-dark-color: var(--neutral-0);
    --white-on-dark-opacity-1: 10%;
    --white-on-dark-opacity-2: 16%;
    --white-on-dark-opacity-3: 28%;
}
