@import url('../../node_modules/@servicepattern/ui/src/color.css');
@import url('./global-css/colors.css');
@import url('./global-css/fonts.css');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  ul,
  ol {
    margin: 14px 0;
    padding-left: 40px;
  }

  ul {
    list-style: disc;
  }

  ol {
    list-style: decimal;
  }
}

@layer utilities {
  /* Is not Firefox */
  @supports not (-moz-appearance:none) {
    .scrollbar-on-hover {
      transition: background-color 0.3s ease;
      background-color: rgba(0, 0, 0, 0);
      background-clip: text;
    }

    .scrollbar-on-hover:hover {
      background-color: rgba(0, 0, 0, 0.25);
    }

    .scrollbar-on-hover::-webkit-scrollbar {
      width: 4px;
    }

    .scrollbar-on-hover::-webkit-scrollbar-track {
      display: none;
    }

    .scrollbar-on-hover::-webkit-scrollbar-thumb {
      background-color: inherit;
    }

    /* We get this scrollbar name from layout */
    .scrollbar-general::-webkit-scrollbar-thumb {
      width: 4px;
      background-color: rgba(227, 228, 232, 1);
      border-radius: 12px;
    }

    .scrollbar-general::-webkit-scrollbar-track {
      display: none;
    }
  }
}
